.container {
	color: #b4a5a5;
	background-color: #3c415c;
	width: 70vw;
	margin-left: auto;
	margin-right: auto;
	margin-top: 1vh;
	margin-bottom: 1vh;
	height: 4vw;
	border-style: none;
	border-radius: 0.3vw;
	position: relative;
	display: inline-block;
	display: flex;
	align-items: center;
	justify-content: center;
	font-family: "Lato", sans-serif;
	font-size: 1vw;
}
