.content {
	color: #b4a5a5;
	background-color: #3c415c;
	margin-left: 10vw;
	font-family: "Lexend", sans-serif;
	border-style: none;
	border-radius: 0.3vw;
	font-size: 24px;
	margin-right: 10vw;
	margin-top: 0.3vh;
	padding-top: 0.03rem;
	padding-bottom: 2rem;
}
@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

.dogImg {
	margin-top: 2rem;
	transform-origin: center center;
	animation-name: spin;
	animation-duration: 1s;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
}
